import rcCalendar from 'shared/reservation-calendar/rc-calendar.component';

export default angular.module('deskworks.BookItNowCalendarCtrl', [
  rcCalendar.name,
])

  .controller('BookItNowCalendarCtrl', function($rootScope, $scope, $translate, state) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('RESERVATIONS.CALENDAR.PAGE_TITLE'));

    $rootScope.$emit('CENTERS:REFRESH'); // force loading centers by state service for displaying center name in header

    /**
     * Next button handler
     */
    $ctrl.next = () => state.setPath('/book-it-now/reservation');
  });
